// import OrdersService from '../services/OrdersService';

const User = {
    isLogged() {
        try {
            return !!upi.User.info.isLogged;
        }
        catch ( e ) {
            return false;
        }
    },

    getUserName: function () {
        return document.head.querySelector( 'meta[name="mUserName"]' ).content;
    }.memoize(),

    redirectToLogin( redirectUrl ) {
        redirectUrl || ( redirectUrl = window.location.href );
        const loginUrl = upi.paths.LOGIN_URL.replace( /page=\/[\w+\d+\.\+%-]+&?/, '' );
        window.location = loginUrl + '&redirectUrl=' + redirectUrl;
        return false;
    },

    getCartCount() {
        return parseInt( $( '#cartLink .items' ).text().replace( /[\(\)]/g, '' ) );
    },
};

export default User;



