import idbKeyval from 'idb-keyval';

const DEFAULT_EXPIRY = 60 * 15; // 15 min

let storage;

if ( 'indexedDB' in window ) {
    storage = {
        _get( key ) {
            return idbKeyval.get( key );
        },
        set( key, val ) {
            // todo: this currently assumes everything is an object - put type check in place to normalize received data
            return idbKeyval.set( key, JSON.stringify( {...val, timestamp: Date.now()} ) );
        },
        delete( key ) {
            return idbKeyval.delete( key );
        },
        clear() {
            return idbKeyval.clear();
        }
    };
}
else {
    storage = {
        _get( key ) {
            return Promise.resolve().then( () => localStorage.getItem( key ) );
        },
        set( key, val ) {
            return Promise.resolve()
                .then( () => localStorage.setItem( key, JSON.stringify( {...val, timestamp: Date.now()} ) ) );
        },
        delete( key ) {
            return Promise.resolve().then( () => localStorage.removeItem( key ) );
        },
        clear() {
            return Promise.resolve().then( () => localStorage.clear() );
        }
    };

}

export const keyvalStore = {
    ...storage,
    get( key, expiry ) {
        let _expiry = expiry || DEFAULT_EXPIRY;
        return this._getOrExpire( key, _expiry );
    },
    _getOrExpire( key, expiry ) {
        return storage._get( key ).then( cached => {
            if ( cached ) {
                const resp = JSON.parse( cached ),
                    age = (Date.now() - resp.timestamp) / 1000;

                if ( age < expiry ) {
                    return resp;
                }

                // cleanup cache if expired
                storage.delete( key );
            }

            return null;
        } );
    }
};



