import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API
} );

// note -> POC for ProductConfiguration Service - this will require refactor after graphQL implementation
export default {

    getOptionGroups ( params ) {
        return svc.get( '/getProductOptionGroups', {params} )
            .then( ServiceProvider.responseData );
    },

    getOptionGroupsCollection ( params ) {
        return svc.get( '/getProductOptionGroupsCollection', {params} )
            .then( ServiceProvider.responseData );
    }

};














