import assign from 'object-assign';
import axios from 'axios';
import {keyvalStore} from './Storage';

import {
    buildCacheKey,
    checkStatus
} from '../../util/RequestUtils';

const AXIOS_DEFAULTS = {
    baseURL: upi.paths.SITE_URL,
    method: 'get',
    timeout: 15000,
    withCredentials: true,
    headers: {
        'Content-Type': 'application/json; charset=utf-8'
        // 'Origin': location.origin
    },
    transformResponse: [
        // checkStatus,
        // data => {
        //     // todo: this may need to be an interceptor
        //     let result = null;
        //
        //     result = ServiceProvider.getResultMetaData( data );
        //
        //     if ( result && result.ReasonCode > 1 ) {
        //         const err = new Error();
        //         err.reasonCode = result.ReasonCode;
        //
        //         switch ( result.ReasonCode ) {
        //             // not authenticated
        //             case 26:
        //                 err.reason = 'noauth';
        //                 break;
        //             default:
        //                 console.warn( 'Unknown request error from ServiceProvider', typeof data );
        //                 err.reason = 'unknown';
        //         }
        //
        //         throw err;
        //     }
        //
        //     return data;
        // }
    ]
};

function bridgeDefaults ( axiosDefault, config = {} ) {
    const {method, params} = config;
    let defaults = {};

    // Move request data from "params" to "data" for POST
    if ( method === 'post' ) {
        defaults.data = params;
    }
    else if ( params ) {
        defaults.params = params;
    }

    delete config.params;

    return assign( {}, axiosDefault, defaults, config );
}

class ServiceProvider {

    constructor ( config ) {
        this.defaults = bridgeDefaults( AXIOS_DEFAULTS, config );
        this._request = axios.create( this.defaults );
    }

    static responseData ( {data} ) {
        return data;
    }

    static getResultMetaData ( data ) {
        // sanitize inconsistent apis
        if ( !data ) {
            return {};
        }

        if ( !data.Result && data.Reason ) {
            data.Result = {
                Reason: data.Reason,
                Status: data.Status,
                ReasonCode: data.ReasonCode || 0
            }
        }

        return data.Result || {};
    }

    /**
     * Facade for ServiceProvider.request configured with 'GET' method
     * this method is cachable by providing "cache: true" in option param
     * @param url
     * @param config
     * @returns {Promise}
     */
    get ( url, config = {} ) {
        return this.request( url, {...config, method: 'get'} );
    }

    /**
     * Facade for ServiceProvider.request configured with 'POST' method
     * @param url
     * @param config
     * @returns {Promise}
     */
    post ( url, config = {} ) {
        return this.request( url, {...config, method: 'post'} );
    }

    request ( url, config = {} ) {
        const axiosReq = bridgeDefaults( this.defaults, config ),
            {method, cache} = axiosReq;

        // check if making CORS requests and ensure support
        if ( axiosReq.baseURL.indexOf( window.location.host ) === -1 && !('withCredentials' in new XMLHttpRequest()) ) {
            throw Error( 'CORS not fully supported' );
        }

        if ( cache && method === 'get' ) {
            return this.cachedRequest( url, axiosReq );
        }

        return this._request( url, axiosReq );
    }

    /**
     * Caches a request in sessionStorage by url + params
     * @param {String} url
     * @param {Object} config ->
     *  baseUrl -> path to webservice
     *  method -> request verb, 'get', 'post'
     *  params -> {}
     *  @return response as promise
     */
    cachedRequest ( url, config = {} ) {
        let {method, params, cacheForUser, cacheExpire} = assign( {}, AXIOS_DEFAULTS, config );
        let cacheKey = buildCacheKey( url, params, cacheForUser );

        try {
            // if response cached under cacheKey -> return it
            return keyvalStore.get( cacheKey, cacheExpire ).then( cached => {
                if ( cached ) {
                    return cached;
                }

                // else issue request to server
                const resp = this._request( url, config );
                // only cache "GET" requests
                if ( method.toLowerCase() === 'get' ) {
                    resp.then( response => {
                        // if request was successful and Valid content-type
                        // application/[json,xml,html,octet-stream] or text/[plain,xml,html]
                        const
                            contentType = response.headers['content-type'],
                            validContentType = /(?:application|text)\/(?:json|xml|html|plain|octet\-stream)/;

                        if ( response.statusText === 'OK' && contentType.match( validContentType ) ) {
                            keyvalStore.set( cacheKey, response );
                        }
                    } );
                }

                return resp;
            } );
        }
        catch ( err ) {
            return this.request( url, config );
        }
    }
}

// assign( upi.namespace( 'Services' ), Events, ServiceProvider.prototype );
export default ServiceProvider;




