import {toQueryString} from './ObjectUtils';
import User from '../modules/User';

export function parseJSON( repsonse ) {
    return repsonse.json();
}

export function parseHTML( response ) {
    return response.text();
}

export function checkStatus( response ) {
    if ( response.ok ) {
        return response;
    }
    else {
        const error = new Error( response.statusText );
        error.response = response;
        throw error;
    }
}

/**
 * Builds a cacheKey to be used for storing $.request with webCache api
 * Consists of the userName (if logged in) and the full path including query string to the api endpoint
 * @param {String} url - path to api
 * @param {Object} params - request params
 * @returns {String} - cacheKey "url?param=value" or "userName:url?param=value" if logged in
 */
export function buildCacheKey( url, params, cacheForUser ) {
    let cacheKey = url;

    if ( cacheForUser && User.isLogged() ) {
        cacheKey = User.getUserName() + ':' + cacheKey;
    }
    if ( params ) {
        cacheKey += '?' + toQueryString( params );
    }

    return cacheKey;
}

/**
 * Get "Status" property of "Result" from a successful request payload from standard upi apis
 * This helper handles inconsistencies that have been introduced from legacy apis
 * @param {Object} data - standard response object from upi api eg { Result: {Status: String} }
 * @returns {*}
 */
export function getResultStatus( data ) {
    // sanitize inconsistent apis
    if ( !data ) {
        return 'fail';
    }
    else if ( data.Result ) {
        return data.Result.Status.toLowerCase();
    }
    else if ( data.Status ) {
        return data.Status.toLowerCase();
    }

    return 'fail';
}




